/*Div Container For The Buttons at the Bottom of the Screen (Filter and Delete Buttons)*/
#tools {
  width: 100%;
  margin:0;
  grid-row: 5 / span 1;
  grid-column: 1 / span 1;
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;
  justify-content: stretch;
  align-items: center;
}

