    display: flex;
    flex-direction: column;
    /*position: absolute;*/
    position: fixed;
    background-color: #f1f1f1;
    min-width: 88px;
    overflow: auto;
    /*top:50%;*/
    /*margin-right: 5px;*/
    /*left: 227px;*/
    right: 2vw;
    margin-right: 30px;
    z-index: 1;
}

.dropdown-content button {
    border-top: 2px solid #664C43;
    border-left: 2px solid #664C43;
    border-right: 2px solid #664C43;
    border-bottom: 0px;
    border-style: solid;
    font-size: 12px;
    font-family: 'Montserrat', sans-serif;
    padding: 4px 8px;
    text-decoration: none;
    /*display: inline;*/
    background-color: #ebe3ca;
}

