@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300&display=swap'); /* For Add Button */


body {
    font-family: 'Montserrat', sans-serif;
}

#item_list {
    width: 100%;
    max-height: 50vh;
    /*fix max height to correct number later*/
    grid-row: 4 / span 1;
    grid-column: 1 / span 1;
    margin-bottom: 0vh;
    display: flex;
    flex-direction: column;
    font-size: 1em;
    overflow-y: auto;
}

#no-items {
    padding-left: 10pt;
    padding-right: 10pt;
    text-align: center;
    font-size: 20px;
}

#add {
    /*margin: 10px 30px 10px 30px;*/
    /*margin-top: 0;*/
    /*margin-bottom: 0;*/
    /*height: 40px;*/
    /* width: 300px;*/
    min-width: 60vw;
    max-width: 70vw;
    line-height: 1em;
    background-color: #664C43;
    color: beige;
    font-size: 2em;
    text-align: center;
    font-family: 'Montserrat', sans-serif;
    border: none;
    border-radius: 5px;
    display: block;
    margin: 0 auto;
}



/* For Mobile Portrait Mode*/
@media only screen and (max-width: 600px) and (orientation: portrait){
    body {
        font-family: 'Montserrat', sans-serif;
    }

    #item_list {
        width: 100%;
        max-height: 50vh;
        /*fix max height to correct number later*/
        grid-row: 4 / span 1;
        grid-column: 1 / span 1;
        margin-bottom: 3.5vh;
        display: flex;
        flex-direction: column;
        font-size: 1em;
        overflow-y: auto;
    }

    #no-items {
        padding-left: 10pt;
        padding-right: 10pt;
        text-align: center;
        font-size: 20px;
    }

    #add {
        /*margin: 10px 30px 10px 30px;*/
        /*margin-top: 0;*/
        /*margin-bottom: 0;*/
        /*height: 40px;*/
        /* width: 300px;*/
        min-width: 60vw;
        max-width: 70vw;
        line-height: 1em;
        background-color: #664C43;
        color: beige;
        font-size: 2em;
        text-align: center;
        font-family: 'Montserrat', sans-serif;
        border: none;
        border-radius: 5px;
        display: block;
        margin: 0 auto;
    }
}

/* Medium devices (landscape tablets, 768px and up) */
@media only screen and (min-width: 992px) {
    #item_list {
        font-size: 1.5em;
        margin-bottom: 5vh;
    }
}

@media only screen and (max-width: 319px)  {
    #item_list {
        max-height: 284px;
        margin-bottom: 0%;
    }

    #add {
        min-width: 192px;
        max-width: 224px;
    }
}
