@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300&display=swap');
@import url('https://fonts.googleapis.com/icon?family=Material+Icons+Outlined');

body {
    font-family: 'Montserrat', sans-serif;
}
/*For the None Option*/
.dropbtn0 {
    background-color: #ebe3ca;
    color: #664C43;
    margin-right: 6px;
    font-size: 16px;
    border: none;
    cursor: pointer;
    width: 24px;
    height: 24px;
}

/*For the Low Option*/
.dropbtn1 {
    background-color: forestgreen;
    color: white;
    margin-right: 6px;
    font-size: 16px;
    border: none;
    cursor: pointer;
    width: 24px;
    height: 24px;
}

/*For the Medium Option*/
.dropbtn2 {
    background-color: yellow;
    color: #664C43;
    margin-right: 6px;
    font-size: 16px;
    border: none;
    cursor: pointer;
    width: 24px;
    height: 24px;
}

/*For the High Option*/
.dropbtn3 {
    background-color: red;
    color: white;
    margin-right: 6px;
    font-size: 16px;
    border: none;
    cursor: pointer;
    width: 24px;
    height: 24px;
}

.dropdown {
    float: right;
    position: relative;
    /*display: inline;*/
}

.dropdown-content {
    display: flex;
    flex-direction: column;
    /*position: absolute;*/
    position: fixed;
    background-color: #f1f1f1;
    min-width: 88px;
    overflow: auto;
    /*top:50%;*/
    /*margin-right: 5px;*/
    /*left: 227px;*/
    right: 2vw;
    margin-right: 30px;
    z-index: 1;
}

.dropdown-content button {
    border-top: 2px solid #664C43;
    border-left: 2px solid #664C43;
    border-right: 2px solid #664C43;
    border-bottom: 0px;
    border-style: solid;
    font-size: 12px;
    font-family: 'Montserrat', sans-serif;
    padding: 4px 8px;
    text-decoration: none;
    /*display: inline;*/
    background-color: #ebe3ca;
}

/*Add outline to the priority button currently pressed*/
#priority-outline {
    box-shadow: 0px 0px 0px 2px #664C43;
}

#high-option {
    border-bottom: 2px solid #664C43;
}


@media only screen and (max-width: 600px) and (orientation: portrait){
    body {
        font-family: 'Montserrat', sans-serif;
    }
    /*For the None Option*/
    .dropbtn0 {
        background-color: #ebe3ca;
        color: #664C43;
        margin-right: 6px;
        font-size: 16px;
        border: none;
        cursor: pointer;
        width: 24px;
        height: 24px;
    }

    /*For the Low Option*/
    .dropbtn1 {
        background-color: forestgreen;
        color: white;
        margin-right: 6px;
        font-size: 16px;
        border: none;
        cursor: pointer;
        width: 24px;
        height: 24px;
    }

    /*For the Medium Option*/
    .dropbtn2 {
        background-color: yellow;
        color: #664C43;
        margin-right: 6px;
        font-size: 16px;
        border: none;
        cursor: pointer;
        width: 24px;
        height: 24px;
    }

    /*For the High Option*/
    .dropbtn3 {
        background-color: red;
        color: white;
        margin-right: 6px;
        font-size: 16px;
        border: none;
        cursor: pointer;
        width: 24px;
        height: 24px;
    }

    .dropdown-content button {
        border-top: 2px solid #664C43;
        border-left: 2px solid #664C43;
        border-right: 2px solid #664C43;
        border-bottom: 0px;
        border-style: solid;
        font-size: 12px;
        font-family: 'Montserrat', sans-serif;
        padding: 4px 8px;
        text-decoration: none;
        display: block;
        background-color: #ebe3ca;
    }

    /*Add outline to the priority button currently pressed*/
    #priority-outline {
        box-shadow: 0px 0px 0px 2px #664C43;
    }

    #high-option {
        border-bottom: 2px solid #664C43;
    }
}

